import React, { Component } from 'react'
import axios from 'axios';
import Moment from 'react-moment';

export class Myattend extends Component {

  constructor(props) {
    super(props)

    this.state = {
      myatten: {},
      msg: {}
    }
  }

  async componentDidMount() {
    try {
      // get token from localStorage
      const token = localStorage.getItem('token');
      //Headers
      const config = {
        headers: {
          'Content-type': "application/json"
        }
      };
      if (token) {
        config.headers['x-auth-token'] = token
      }
      const res = await axios.get('/api/myattend/user', config);
      if (res.status === 200) {
        this.setState({ myatten: res.data })

      }
    } catch (error) {
      if (error.response.status === 401) {
        this.setState({ loggedIn: false })
        localStorage.removeItem('token');
        this.props.history.push('/login')
      }

    }
  }
  render() {
    const { myatten } = this.state
    return (
      <div className="container-fluid">
        <div className="row m-right">
          <div className="col-md-10 m-auto">
          {
                    myatten.length? myatten.map((post, index) => {
                        return (

                  <div className="card shadow my-5">
                    <div className="card-header bg-success">
                    <small class="text-light">{index+1}</small>
                      <h3 className="text-center text-light">
                        Your Attendence
                      </h3>
                    </div>
                    <div className="card-body">
                <hr />
                <p className="card-text">
                  <b>Latitude</b><span className="float-end">{post.latitude}</span>
                </p>
                <p className="card-text">
                  <b>Longitude</b><span className="float-end">{post.longitude}</span>
                </p>
                <p className="card-text">
                  <b>Altitude</b><span className="float-end">{post.altitude}</span>
                </p>
                <p className="card-text">
                  <b>Address</b><span className="float-end">{post.address}</span>
                </p>
                
                <p className="card-text">
                  <b>Date</b><span className="float-end"><Moment format='lll'>{post.date}</Moment></span>
                </p>
              </div>

                  </div>
                   )
                  }):null
              }
                  </div>
                  
        </div>
      </div>
     
    )



  }
}

export default Myattend