import React, { Component } from 'react'
import axios from 'axios';
import Moment from 'react-moment';

export class Movesreported extends Component {
    constructor(props) {
      super(props)
    
      this.state = {
         mypost: {},
         msg: {}
      }
    }

    async componentDidMount() {
        try {
          // get token from localStorage
          const token = localStorage.getItem('token');
          //Headers
          const config={
            headers: {
              'Content-type':"application/json"
            }
          };
          if (token) {
            config.headers['x-auth-token'] = token
          }
          const res = await axios.get('/api/mypost/user', config);
          if (res.status === 200) {
            this.setState({mypost: res.data})
            
          }
        } catch (error) {
          if (error.response.status === 401) {
            this.setState({loggedIn : false})
            localStorage.removeItem('token');
            this.props.history.push('/login')
          }
          
        }
          }
  render() {
    const {mypost} = this.state
    return (
      <div className="container">
        <div className="row">
            <div className="col-md-10 m-auto">
                {
                    mypost.length? mypost.map((post, index) => {
                        return (
                    
                <div className="card shadow my-5">
                    <div className="card-header bg-success">
                        <small class="text-light">{index+1}</small>
                        <h3 className="text-light">
                            Your Reported Moves
                        </h3>
                </div>
                <div className="card-body">
                  <hr />
                  <p className="card-text">
                  <b>Reason</b><span className="float-end">{post.reason}</span>
                </p>
                <p className="card-text">
                  <b>Activity</b><span className="float-end">{post.activity}</span>
                </p>
                <p className="card-text">
                  <b>Location</b><span className="float-end">{post.location}</span>
                </p>
                
                <p className="card-text">
                  <b>Date</b><span className="float-end"><Moment format='lll'>{post.date}</Moment></span>
                </p>
                    
                </div>
                </div>
                        )
                        }):null
                    }
            </div>
        </div>
      </div>
    )
  }
}

export default Movesreported