import React, { Component } from 'react'
import axios from 'axios';
import Moment from 'react-moment';

export class Viewattend extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       allpost: {},
       msg: {}
    }
  }
  async componentDidMount() {
      try {
        const res = await axios.get('/api/all/attendance');
            if (res.status === 200) {
              this.setState({allpost: res.data})
              console.log(res.data);
              
            }
          } catch (error) {
            if(error){
                console.log(error)
            }
          }
        }
  render() {
    const {allpost}=this.state;
    return (
      <div className="container-fluid">
        <div className="row m-right">
            
            <div className="col-md-10 m-auto">
                {
                    allpost.length? allpost.map((post, index) => {
                        return (
                    
                <div className="card shadow my-5">
                    <div className="card-header bg-success">
                        <small class="text-light">{index+1}</small>
                        <h3 className="text-center text-light">
                            Student's Attendence
                        </h3>
                </div>
                <div className="card-body">
                  <hr />
                  <p className="card-text">
                  <b>UserName</b><span className="float-end">{post.attendance_markedBy}</span>
                </p>
                  <p className="card-text">
                  <b>Latitude</b><span className="float-end">{post.latitude}</span>
                </p>
                <p className="card-text">
                  <b>Longitude</b><span className="float-end">{post.longitude}</span>
                </p>
                <p className="card-text">
                  <b>Altitude</b><span className="float-end">{post.altitude}</span>
                </p>
                <p className="card-text">
                  <b>Address</b><span className="float-end">{post.address}</span>
                </p>
                
                <p className="card-text">
                  <b>Date</b><span className="float-end"><Moment format='lll'>{post.date}</Moment></span>
                </p>
                    
                </div>
                </div>
                        )
                        }):null
                    }
            </div>
        </div>
      </div>
    )
  }
}

export default Viewattend